nav {
    width: 100%;
    height: fit-content;
    position: fixed;
    margin-top: -3rem;
    z-index: 9999;

    .nav__container {
        width: 100%;
        height: 3rem;
        background-color: rgba(149, 150, 153, 0.384);
        backdrop-filter: blur(10px);

        .menu {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-evenly;
            align-items: center;

            .menu-item {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 40px;
                width: 70px;

                a {
                    font-size: 1.3rem;
                    color: var(--accent-color);
                }
            }
        }
    }
}